import React from "react";
import { HashRouter as Router, Route } from "react-router-dom";

import Menu from "./components/menu";
import Header from "./components/header";
import Footer from "./components/footer";
import ProjectThumbs from "./components/projectThumbs";
import About from "./components/about";
import Class from "./components/class";

/** Project Pages */
import Summoner from "./components/projects/summoner";
import FluidGraphics from "./components/projects/fluidgraphics";
import DegreesOfSuccess from "./components/projects/DegreesOfSuccess";

import "./App.css";

const pages = ["about", "classes"];
//"writing", "resume"

const Work: React.FC = () => {
  return (
    <>
      <Header />
      <ProjectThumbs />
    </>
  );
};

interface State {
  lookingAt: string;
}

class App extends React.Component<{}, State> {
  state = { lookingAt: "work" };

  componentDidMount() {
    document.title = "Jessica Tang";
  }

  changePage = (page: string) => {
    this.setState({ lookingAt: page });
    window.history.pushState({}, "", `/${page}`);
  };

  render() {
    return (
      <Router>
        <Menu pages={pages} />
        <Route path="/" exact component={Work} />
        <Route path="/about/" component={About} />
        <Route path="/classes/" component={Class} />
        <Route path="/projects/summoner/" component={Summoner} />
        <Route path="/projects/fluidgraphics/" component={FluidGraphics} />
        <Route path="/projects/dos/" component={DegreesOfSuccess} />
        <Footer />
      </Router>
    );
  }
}

export default App;
