import React from "react";
import { socialLinks } from "./utils";

import "./header.css";

interface iconProps {
  icon: string;
  link: string;
  alt?: string;
}

const Icon = ({ icon, link, alt }: iconProps) => {
  return (
    <a href={link} target="_blank" rel="noopener noreferrer">
      <img className="header-icon" src={icon} alt={alt || icon} />
    </a>
  );
};

const Header: React.FC = () => {
  const whoIAm = () => {
    return "jessica";
  };

  return (
    <div className="header-container">
      <h1 className="header-title">hi, i'm {whoIAm()}</h1>
      <div className="header-social">
        {socialLinks.map((s, i) => (
          <Icon key={i} icon={s.icon} link={s.link} />
        ))}
      </div>
      <div className="header-blurb">
        i'm a senior at MIT studying media studies {"&"} computer science. i
        like turning code into magic
      </div>
    </div>
  );
};

export default Header;
