import React from "react";
import { Link as RouterLink } from "react-router-dom";
import { Link } from "./utils";

import "./thumb.css";

const Thumb = props => {
  return (
    <div className="thumb-container">
      {props.imageURL.indexOf("mp4") > -1 ? (
        <video className="thumb-art" autoplay="" loop>
          <source type="video/mp4" src={props.imageURL} />
        </video>
      ) : (
        <img className="thumb-art" src={props.imageURL} alt={props.name} />
      )}
      <div className="thumb-hoverContainer">
        <div className="thumb-title">{props.name}</div>
        {props.blurb && <div className="thumb-blurb">{props.blurb}</div>}
        <div className="thumb-linkContainer">
          {props.refLinks &&
            props.refLinks.map((l, i) => (
              <Link
                key={i}
                className="thumb-link"
                link={l.link}
                name={l.name}
              />
            ))}
          {props.internalURL && (
            <RouterLink className="thumb-link" to={props.internalURL}>
              writeup
            </RouterLink>
          )}
          {!props.refLinks && !props.internalURL && (
            <div className="thumb-soon">project info coming soon</div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Thumb;
