import React from "react";
import { Link as RouterLink } from "react-router-dom";

import { Link } from "./utils";

import "./menu.css";

interface MenuProps {
  pages: string[];
}

class Menu extends React.Component<MenuProps> {
  render() {
    return (
      <div className="menu-container">
        <div>
          <RouterLink to="/" className="u-pointer menu-link">
            work
          </RouterLink>
        </div>
        <div className="menu-right">
          {this.props.pages.map((p, i) => (
            <RouterLink key={i} className="u-pointer menu-link" to={`/${p}/`}>
              {p}
            </RouterLink>
          ))}
          <Link
            className="menu-link"
            name="resume"
            link={process.env.PUBLIC_URL + "/Jessica_Tang_Resume.pdf"}
          />
        </div>
      </div>
    );
  }
}

export default Menu;
