import React from "react";
import classnames from "classnames";
import Thumb from "./thumb";
import { projects } from "./utils";

import "./projectThumbs.css";

interface ProjectThumbState {
  filter: string;
  onMobile: boolean;
}

const projectTypes = ["all", "code", "design", "game"];

class ProjectThumbs extends React.Component<{}, ProjectThumbState> {
  state = { filter: "all", onMobile: false };

  componentDidMount() {
    window.addEventListener("resize", this.resize.bind(this));
    this.resize();
  }

  resize() {
    if (window.innerWidth <= 760) {
      this.setState({ onMobile: true });
    } else {
      this.setState({ onMobile: false });
    }
  }

  setFilter = (val: string) => {
    this.setState({ filter: val });
  };

  getProjects = () => {
    const selectProjects = [];
    for (let p of projects) {
      if (
        this.state.filter === "all" ||
        (p.tags && p.tags.includes(this.state.filter))
      ) {
        selectProjects.push(p);
      }
    }
    return selectProjects;
  };

  render() {
    const selectProjects = this.getProjects();

    return (
      <div className="projectThumbs-container">
        <div className="projectThumbs-filterContainer">
          {projectTypes.map((t, i) => (
            <span
              key={i}
              className={classnames("projectThumbs-filter", "u-pointer", {
                "projectThumbs-filter--selected": this.state.filter === t,
              })}
              onClick={() => {
                this.setFilter(t);
              }}
            >
              {t}
            </span>
          ))}
        </div>
        <div className="projectThumbs-thumbContainer">
          <div className="projectThumbs-column">
            {selectProjects.map(
              (p, i) =>
                p &&
                (i % 2 === 0 || this.state.onMobile) && (
                  <Thumb
                    key={`r${i}`}
                    imageURL={p.imageURL}
                    name={p.name}
                    blurb={p.blurb}
                    refLinks={p.refLinks}
                    internalURL={p.internalURL}
                  />
                ),
            )}
          </div>
          <div className="projectThumbs-column">
            {!this.state.onMobile &&
              selectProjects.map(
                (p, i) =>
                  p &&
                  i % 2 !== 0 && (
                    <Thumb
                      key={`l${i}`}
                      imageURL={p.imageURL}
                      name={p.name}
                      blurb={p.blurb}
                      refLinks={p.refLinks}
                      internalURL={p.internalURL}
                    />
                  ),
              )}
          </div>
        </div>
      </div>
    );
  }
}

export default ProjectThumbs;
